.component.nova-poshta-addr-selector {
	opacity: 0;
	visibility: hidden;
	height: 0;
	transition-duration: $trans-du;
	transition-property: visibility, opacity, height;
	position: relative;

	&.show {
		opacity: 1;
		visibility: visible;
		height: max-content;
	}

	.addr-inp {
		padding-right: 40px;
	}

	.component.preloader {
		&.addr-inp-preloader {
			position: absolute;
			right: 10px;
			top: 24px;

			@include preloader-params(2px, $color-text);
			@include preloader-size-sm();
		}

		&.nova-poshta-department-number-selector-preloader {
			position: absolute;
			width: 100%;
			text-align: center;

			@include preloader-params(3px, $color-text-lite);
		}
	}

	.variants-addrs {
		overflow-y: auto;
		max-height: 200px;
		position: absolute;
		left: 0;
		top: auto;
		margin-top: $std-mg-md;
		z-index: 1;
		width: 100%;
		box-shadow: $shadow-sm;
		border-radius: $border-rad-lg;
		opacity: 0;
		visibility: hidden;
		transition-duration: $trans-du;
		transition-property: visibility, opacity;

		&.show {
			opacity: 1;
			visibility: visible;
		}

		&::-webkit-scrollbar {
			width: 7px;
		}

		.addr-item {}

		.msg-container {
			background-color: $color-bg-card;	
			border-radius: $border-rad-lg;
			width: 100%;
			text-align: center;
			padding: $std-pd-md 0;
		}
	}

	.nova-poshta-department-number-selector-group {
		opacity: 0;
		visibility: hidden;
		display: none;
		transition-duration: $trans-du;
		transition-property: opacity, visibility;

		&.show {
			opacity: 1;
			visibility: visible;
		}
	}

	.component.select {
		.selector {
			.clickable-list {
				max-height: 200px;
			}
		}
	}
}